<template>
  <div class="pa-3">
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md8>
          <v-card class="elevation-12">
            <v-toolbar dark>
              <v-toolbar-title class="white--text">GitLab API Configuration</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <div class="body-1 mb-3">
                You must define the API Token and the GitLab URL you want to use. More information on how to set this is available <a href="https://docs.gitlab.com/ee/user/profile/personal_access_tokens.html" target="_blank">on the GitLab documentation</a>.
                Nothing is sent to any other server than the one you define here.
                Everything is stored locally, right in your browser.
              </div>
              <v-form ref="form" v-model="valid">
                <v-text-field
                  v-model="apiToken"
                  label="API Token"
                  :rules="tokenRules"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="apiEndpoint"
                  label="Gitlab URL"
                  :rules="urlRules"
                  required
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                :disabled="!valid"
                @click="submit">
                Go!
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  name: "settings",
  data() {
    return {
      valid: false,
      tokenRules: [
        v => !!v || 'Token is required'
      ],
      urlRules: [
        v => !!v || 'URL is required',
        v => /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/.test(v) || 'URL is not valid'
      ],
      apiToken: null,
      apiEndpoint: null
    };
  },
  mounted() {
    this.apiToken = this.$store.state.apiToken;
    this.apiEndpoint = this.$store.state.apiEndpoint;
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch("updateSettings", {
          apiToken: this.apiToken,
          apiEndpoint: this.apiEndpoint
        });
        this.$router.push("/");
      }
    }
  },
  components: {}
};
</script>
